@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap');


.About_us{
    display: block;
    height: 100%;
   
   
    color: #0c3b98;
    text-align: center;
    position: relative;
   
    padding: 0px var(--app-block-padding);
      
}
.services_body{
   margin-top: 70px;
}
.top_icons{
   
   display: flex;
   justify-content: space-between;
}

.icon_div{
    width: 100%;
   
}
.About_us h1{
    font-family: 'Courier New', Courier, monospace;
    font-size: 44px;
font-style: normal;
font-weight: 900;
line-height: normal;
color: var(--Accent-Main-Accent, #073169);
}
.About_us span{
    background: var(--Linear, linear-gradient(90deg, #16CDAC 0.21%, #0C3B98 99.79%));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 900;
    font-size: 64px;
    
}

.services_heading{
    padding: 20px;
}
.services_div{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 4em;
    height: 100%;
  padding: 20px;
   margin: 2px 1em; 
   
}
.About_us h4{
    font-size: 18px;
    font-weight: 800;
  line-height: 27px
}
.About_us p{
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    margin-top: 5px;
    color: #3975e8;
    padding-right: 2px;
    font-family: Nunito;

}
.About_us img{
    width: 150px;
    height: 123px;
}

.Digital_care{
   display: block;
   justify-content: space-between;
   gap: 10px;
   align-items: center;
   padding-left: 8px;
   padding-right: 8px;
   background: var(--Primary-01, #D0F2EE);
    border-radius: 20px;
    color: #172048;
    padding-bottom: 10px;
}
.Digital_care p{
    color:  #172048;
    
}
.Digital_care p:hover{
 color: #fff;
}
.Digital_care:hover{
    background: linear-gradient(170deg, #16CDAC -9.82%, #0C3B98 137.55%);
    transform: translateY(-7px);
    color: #fff;
}

