.Doctor_connect{
    display: block;
    justify-content: space-between;
    gap: 10px;
    align-items: center;
    padding-left: 8px;
    padding-right: 8px;
    background: var(--Primary-01, #D0F2EE);
     border-radius: 20px;
     color: #172048;
 }
 .Doctor_connect p{
     color:  #172048;
     
 }
 .Doctor_connect p:hover{
  color: #fff;
 }
 .Doctor_connect:hover{
     background: linear-gradient(170deg, #16CDAC -9.82%, #0C3B98 137.55%);
     transform: translateY(-7px);
     color: #fff;
 }

.pharmacy_services{
    display: block;
    justify-content: space-evenly;
   align-items: left;
 
    border-radius: 20px;
    padding-right: 8px;
    background: var(--Primary-01, #D0F2EE);
     color: #172048;
}
.pharmacy_services:hover{
    background: linear-gradient(170deg, #16CDAC -9.82%, #0C3B98 137.55%);

    transform: translateY(-7px);
    color: #fff;
}
.pharmacy_services p{
    color:  #172048;
    
}
.pharmacy_services p:hover{
 color: #fff;
}


.ambulance_services{
    display: block;
    background: var(--Primary-01, #D0F2EE);
    border-radius: 20px;
    justify-content: space-evenly;
   gap: 10px;
   align-items: center;
   padding-left: 8px;
   padding-right: 8px;
   color:  #172048;
}
.ambulance_services:hover{
    background: linear-gradient(170deg, #16CDAC -9.82%, #0C3B98 137.55%);
   
    transform: translateY(-7px);
    color: #fff;
}
.ambulance_services p{
    color:  #172048;
    
}
.ambulance_services p:hover{
 color: #fff;
}


@media (min-width: 767px) and (max-width: 1023px){
    .About_us{
        width: 150%;
    }
}

@media (min-width: 431px)and  (max-width: 765px){
    .About_us{
        width: 150%;
    }
    .About_us span{
        background: var(--Linear, linear-gradient(90deg, #16CDAC 0.21%, #0C3B98 99.79%));
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: 'Courier New', Courier, monospace;
        font-weight: 900;
        font-size: larger;
    }
    
    .services_div{
        display: block;
        justify-content: space-between;
      
    }
    .About_us h1{
    font-size: 34px;
    }
    .services_div p{
        font-size: 20px;
        padding-bottom: 10px;
        
    }
    .top_icons{
       display: none;
    }
    .Digital_care{
        margin-bottom: 20px;
    }
    .pharmacy_services{
        margin-bottom: 20px;
    }
    .Doctor_connect{
        margin-bottom: 20px;
    }
   
  }
